/*------------------------------------*\
    Spacing variables
    - For generic block spacing try
    to only use margin bottom
    for consistancy
\*------------------------------------*/
:root {
    /*------------------------------------*\
    Paddings
    \*------------------------------------*/
    --col-pd-wrapper: 0 2.4rem;
    --col-pd-inner: 0 2.4rem;

    // --col-pd-wrapper-small: 0 1rem;
    --col-pd-inner-small: 0 3.9rem;

    --col-pd-inner-medium: 0 16.5rem;

    --h-gutter-lg: 16.5rem;
    --v-spacing: 8.2rem 0;

    /*------------------------------------*\
    Margins
    \*------------------------------------*/
    --section-mb-sm: 3rem;
    --section-mb-md: 8rem;
    --section-mb-lg: 9rem;
    --section-mb-xl: 12rem;
}
